import { html, TemplateResult } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { classMap } from 'lit/directives/class-map.js';

import { Base } from '../base';

type StageType = 'none' | 'button' | 'link' | 'dropdown' | 'dropdownLink';

@customElement('zwc-stage-carousel')
export class ZwcStageCarousel extends Base {
  @property({ type: String })
  type: StageType = 'none';

  @property({ type: String })
  headline = 'Welcome to';

  @property({ type: String })
  emphasized = 'Zurich Insurance';

  @property({ type: String })
  subline = 'Zurich Insurance';

  @property({ type: Boolean, attribute: 'pagination-right' })
  paginationRight = false;

  @property({ type: Boolean })
  inverted = false;

  protected override render(): TemplateResult {
    const classes = {
      'stage-carousel': true,
      'stage-carousel--pagination-right': this.paginationRight,
      'stage-carousel--inverted': this.inverted,
    };

    return html`
      <div class=${classMap(classes)}>
        <div
          class="splide"
          aria-labelledby="stage-headline"
          aria-roledescription="carousel"
          role="group"
        >
          <div class="splide__track">
            <ul class="splide__list">
              <li class="splide__slide">item</li>
              <li class="splide__slide">item</li>
            </ul>
          </div>
        </div>
      </div>
    `;
  }

  // <!-- ${generateSplideArrows({ halfCircle, icon: !halfCircle ? 'short' : '' })} -->
  // <!-- <li class="splide__slide">${StageStory}</li> -->
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-stage-carousel': ZwcStageCarousel;
  }
}
